import React, {
  useState,
  useRef,
  useEffect,
  forwardRef,
  useImperativeHandle,
} from "react";
import {
  ButtonContainer,
  IconButton,
  Status,
  AudioPlayer,
  InfoButton,
  RecordIcon,
} from "./styles";

const AudioRecorderPage = forwardRef(({ triggerReset }, ref) => {
  const [permission, setPermission] = useState(false);
  const [stream, setStream] = useState(null);
  const [isRecording, setIsRecording] = useState(false);
  const [audioURL, setAudioURL] = useState("");
  const mediaRecorder = useRef(null);
  const audioChunks = useRef([]);
  const audioPlayerRef = useRef(null);
  const stopResolver = useRef(null);
  const playResolver = useRef(null);

  const getMicrophonePermission = async () => {
    if ("MediaRecorder" in window) {
      try {
        const streamData = await navigator.mediaDevices.getUserMedia({
          audio: true,
        });
        setPermission(true);
        setStream(streamData);
        return streamData; // Return stream on success
      } catch (err) {
        alert(err.message);
        return null; // Return null on error
      }
    } else {
      alert("");
      return null;
    }
  };

  // Expose functions to parent component
  useImperativeHandle(ref, () => ({
    startRecording,
    stopRecording: () => {
      return new Promise((resolve) => {
        stopResolver.current = resolve;
        stopRecordingInternal();
      });
    },
    play: () => {
      return new Promise((resolve) => {
        if (audioPlayerRef.current) {
          playResolver.current = resolve;
          audioPlayerRef.current.play();
        } else {
          resolve();
        }
      });
    },
    getMicrophonePermission,
  }));

  // Reset function
  const resetRecorder = () => {
    //  recorder
    if (mediaRecorder.current) {
      try {
        mediaRecorder.current.stop();
      } catch (e) {
        // 
      }
      mediaRecorder.current = null;
    }

    //  reset 
    if (permission) {
      if (audioURL) {
        URL.revokeObjectURL(audioURL); //  blob URL
      }
      setIsRecording(false);
      setAudioURL("");
      audioChunks.current = [];
    } else {
      //   
      setPermission(false);
      setStream(null);
      setIsRecording(false);
      if (audioURL) {
        URL.revokeObjectURL(audioURL);
      }
      setAudioURL("");
      audioChunks.current = [];
    }
  };

  useEffect(() => {
    if (triggerReset) {
      resetRecorder();
    }
  }, [triggerReset]);

  const startRecording = async () => {
    let currentStream = stream;
    if (currentStream === null) {
      currentStream = await getMicrophonePermission();
    }

    if (!currentStream) return; // Don't start if permission is denied

    setIsRecording(true);
    if (audioURL) {
      URL.revokeObjectURL(audioURL); //  URL
    }
    setAudioURL("");
    audioChunks.current = [];

    const media = new MediaRecorder(currentStream, { type: "audio/webm" });
    mediaRecorder.current = media;
    mediaRecorder.current.start();

    mediaRecorder.current.ondataavailable = (event) => {
      if (event.data && event.data.size > 0) {
        audioChunks.current.push(event.data);
      }
    };
  };

  const stopRecordingInternal = () => {
    setIsRecording(false);
    if (mediaRecorder.current && mediaRecorder.current.state !== "inactive") {
      mediaRecorder.current.stop();

      mediaRecorder.current.onstop = () => {
        const audioBlob = new Blob(audioChunks.current, { type: "audio/webm" });
        const audioUrl = URL.createObjectURL(audioBlob);
        setAudioURL(audioUrl);
        audioChunks.current = [];
      };
    } else {
      if (stopResolver.current) {
        stopResolver.current();
        stopResolver.current = null;
      }
    }
  };

  const stopRecording = () => {
    stopRecordingInternal();
  };

  return (
    <ButtonContainer>
      {/*  -  */}
      {!permission && navigator.permissions && (
        <InfoButton onClick={getMicrophonePermission}>
           
        </InfoButton>
      )}

      {/*  */}
      {permission && !isRecording && !audioURL && (
        <InfoButton onClick={startRecording}>
          <RecordIcon recording={false} /> 
        </InfoButton>
      )}

      {/*  */}
      {permission && isRecording && (
        <>
          <IconButton onClick={stopRecording}>  </IconButton>
          <RecordIcon recording={true} />
          <Status>... </Status>
        </>
      )}

      {/*  bar */}
      {permission && !isRecording && audioURL && (
        <AudioPlayer
          ref={audioPlayerRef}
          src={audioURL}
          controls
          onCanPlayThrough={() => {
            if (stopResolver.current) {
              stopResolver.current();
              stopResolver.current = null;
            }
          }}
          onEnded={() => {
            if (playResolver.current) {
              playResolver.current();
              playResolver.current = null;
            }
          }}
        />
      )}
    </ButtonContainer>
  );
});

export default AudioRecorderPage;
