import { StrictMode } from "react";
import { createRoot } from "react-dom/client";
import { createBrowserRouter, RouterProvider } from "react-router-dom";
import { AppProvider } from "./store/contexts/AppContext"; // Import AppProvider

import App from "./components/App"; // Import App component
import HomePage from "./pages/home";
import Quiz from "./pages/quiz/components/Quiz";
import FillInQuiz from "./pages/quiz/components/FillInQuiz";

import SystemSettingsPage from "./pages/systemSettings";
import NotebookManagementPage from "./pages/notebookManagement";
import NavigationBlocker from "./components/NavigationBlocker";
import Reading from "./pages/Reading/components/Reading";
import WordTest from "./pages/wordTest"; // Anki Demo

const rootElement = document.getElementById("root");
const root = createRoot(rootElement);

const router = createBrowserRouter(
  [
    {
      path: "/",
      element: <App />,
      children: [
        {
          index: true,
          element: <HomePage />,
        },
        {
          path: "quiz",
          element: (
            <NavigationBlocker
              clearOnConfirm
              considerQuiz={false}
              considerReading={true}
              considerFillin={true}
              message=""
            >
              <Quiz />
            </NavigationBlocker>
          ),
        },
        {
          path: "fillin",
          element: (
            <NavigationBlocker
              clearOnConfirm
              considerQuiz={true}
              considerReading={true}
              considerFillin={false}
              message=""
            >
              <FillInQuiz />
            </NavigationBlocker>
          ),
        },
        {
          path: "word_test",
          element: (
            <NavigationBlocker
              clearOnConfirm
              message=""
            >
              <WordTest />
            </NavigationBlocker>
          ),
        },
        {
          path: "notebook-management",
          element: (
            <NavigationBlocker
              clearOnConfirm
              message=""
            >
              <NotebookManagementPage />
            </NavigationBlocker>
          ),
        },
        {
          path: "settings",
          element: (
            <NavigationBlocker
              clearOnConfirm
              message=""
            >
              <SystemSettingsPage />
            </NavigationBlocker>
          ),
        },
        {
          path: "reading",
          element: (
            <NavigationBlocker
              clearOnConfirm
              considerQuiz={true}
              considerReading={false}
              considerFillin={true}
              message=""
            >
              <Reading />
            </NavigationBlocker>
          ),
        },
      ],
    },
  ],
  {
    basename: process.env.PUBLIC_URL,
  }
);

root.render(
  <StrictMode>
    <AppProvider>
      <RouterProvider router={router} />
    </AppProvider>
  </StrictMode>
);
